<template>
  <dl class="loginForm">
    <dt>Login</dt>
    <dd>
      <div class="fieldBox">
        <v-form ref="form" v-model="valid" lazy-validation>
          <v-text-field
            prepend-inner-icon="mdi-account-card-outline"
            v-model="uid"
            :counter="20"
            :rules="nameRules"
            label="Id"
            required
          ></v-text-field>
          <v-text-field
            v-model="password"
            prepend-inner-icon="mdi-lock-outline"
            :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[passwordRules.required, passwordRules.min]"
            :type="showPassword ? 'text' : 'password'"
            name="input-10-2"
            label="Password"
            hint="At least 8 characters"
            value=""
            class="input-group--focused"
            @click:append="showPassword = !showPassword"
          ></v-text-field>
          <!-- <v-checkbox v-model="checkbox" label="아이디 저장"></v-checkbox> -->
        </v-form>
      </div>

      <div class="buttonBox">
        <v-btn
          large
          block
          :disabled="!valid"
          :loading="loading"
          color="primary"
          @click="loader = 'loading'"
        >
          로그인
        </v-btn>
      </div>
    </dd>
  </dl>
</template>

<script>
import { ref, watch } from "vue";
import { useAuthStore } from "@/stories/mainStore.js";
import { logIn } from "@/services/user.js";

export default {
  setup() {
    //
    const authStore = useAuthStore();

    //
    const form = ref(null);
    const uid = ref("");
    const password = ref("");
    const loader = ref(null);
    const loading = ref(false);
    const valid = ref(true);
    const showPassword = ref(false);
    const nameRules = [
      (v) => !!v || "Id is required",
      (v) => (v && v.length <= 20) || "Id must be less than 20 characters",
    ];
    const passwordRules = {
      required: (value) => !!value || "Password is required.",
      min: (v) => v.length >= 8 || "Min 8 characters",
    };
    const userLogin = async () => {
      // const data = await auth();
      // const autoObj = {}
      // console.log(this);
      const { state } = await logIn(uid.value, password.value);
      if (state === "00") {
        authStore.setAuth(true);
      } else {
        console.log("다시");
      }
    };

    watch(loader, () => {
      const validate = form.value.validate();

      //   this.$refs.form.reset();
      //   this.$refs.form.resetValidation();

      if (validate === true) {
        loading.value = true;
        userLogin();
        // setTimeout(() => {
        //   this[l] = false;
        //   this.authStore.setAuth(true);
        // }, 1000);
      }
    });

    return {
      form,
      uid,
      password,
      loader,
      loading,
      valid,
      showPassword,
      nameRules,
      passwordRules,
      authStore,
    };
  },
};
</script>

<style>
/* dl.loginForm {
} */
dl.loginForm > dt {
  font-size: 28px;
  font-weight: bold;
  text-align: center;
}
dl.loginForm > dd {
  margin-top: 50px;
}

.v-application--is-ltr .v-text-field .v-input__prepend-inner {
  margin-right: 10px;
  margin-left: 10px;
}
.fieldBox {
  width: 40vw;
  max-width: 500px;
  min-width: 300px;
}

.buttonBox {
  margin-top: 20px;
}

.v-text-field + .v-text-field {
  margin-top: 10px;
}

custom-loader {
  animation: loader 1s infinite;
  display: flex;
}
@-moz-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
