<script>
import LoginComponent from "@/components/LoginComponent";
import MainFrame from "@/views/MainFrame";

import { useAuthStore } from "@/stories/mainStore.js";
import { auth } from "@/services/user.js";

export default {
  data: () => ({
    selectedItem: null,
    drawer: true,
    group: null,
    defaultPermission: "master",
    permissions: ["master", "visitor"],
    pages: [
      {
        title: "home",
        value: "home",
        to: "/",
      },
      {
        title: "tracking",
        value: "tracking",
        to: "/tracking",
      },
      {
        title: "voyage",
        value: "voyage",
        to: "/voyage",
      },
      {
        title: "data",
        value: "data",
        to: "/data",
      },
      {
        title: "alam",
        value: "alam",
        to: "/alam",
      },
      {
        title: "solution",
        value: "solution",
        to: "/solution",
      },
      {
        title: "diagnostics",
        value: "diagnostics",
        to: "/diagnostics",
      },
      {
        title: "setting",
        value: "setting",
        to: "/setting",
      },
      {
        title: "master",
        value: "master",
        to: "/master",
      },
    ],
  }),
  components: {
    LoginComponent,
    MainFrame,
  },
  setup() {
    const authStore = useAuthStore();

    return {
      authStore,
      checkAuth: async () => {
        // const data = await auth();
        // const autoObj = {}
        const { state } = await auth();
        if (state === "00") {
          authStore.setAuth(true);
        } else {
          authStore.setAuth(false);
        }
      },
    };
  },
  created() {
    this.checkAuth();
  },
  watch: {
    group() {
      this.drawer = true;
    },
  },
};
</script>
