import { defineStore } from "pinia";
import {
  ref,
  // computed
} from "vue";

export const useAuthStore = defineStore("auth", () => {
  //
  const uid = ref("");
  const errorState = ref("");
  const isAuth = ref(null);
  function setAuth(state) {
    isAuth.value = state;
  }
  return { uid, errorState, isAuth, setAuth };
});
